import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import PropTypes from 'prop-types';
import { createSelector } from 'reselect';
import get from 'lodash/get';
import { Row, Col } from 'reactstrap';
import TokenManager from 'utils/TokenManager';
import Loader from 'components/Shared/Loader';
import InputField from 'components/Shared/InputField';
import { validateEmail } from 'utils/validations';
import * as loginActions from './actions';
import { makeSelectUserDetails, makeSelectUserErrorLogin, makeSelectIsFetching } from './selectors';
import './styles.scss';

class Login extends React.Component {
    constructor() {
        super();
        this.state = {
            email: {
                value: '',
                valid: true,
                error: '',
            },
            password: {
                value: '',
                valid: true,
                error: '',
            },
        };
    }

    componentWillMount() {
        if (TokenManager.get()) {
            this.props.history.push('/');
        }
    }

    componentWillUnmount() {
        this.props.resetUserDetails();
    }

    onSubmit = e => {
        e.preventDefault();
        const { email, password } = this.state;
        const error = 'This field is required';
        if (email.valid && password.valid) {
            this.props.loginUser({
                email: email.value,
                password: password.value,
            }).then(response => {
                TokenManager.set(get(response, 'payload.data.token', ''));
                this.props.history.push('/');
            });
        } else if (!email.value && !password.value) {
            this.setState({
                email: {
                    ...this.state.email,
                    error,
                    valid: false,
                },
                password: {
                    ...this.state.password,
                    error,
                    valid: false,
                },
            });
        }
        if (!password.value) {
            this.setState({
                password: {
                    ...this.state.password,
                    error,
                    valid: false,
                },
            });
        }
        if (!email.value) {
            this.setState({
                email: {
                    ...this.state.email,
                    error,
                    valid: false,
                },
            });
        }
    };

    handleOnChange = e => {
        e.preventDefault();
        const newState = {};
        const { value, name } = e.target;
        const isEmailValid = validateEmail(value);
        if (name === 'email') {
            newState[name] = {
                value,
                valid: isEmailValid,
                error: !isEmailValid ? 'Please enter valid email format' : '',
            };
        } else if (!value) {
            newState[name] = {
                value: '',
                error: 'This field is required',
            };
        } else {
            newState[name] = {
                value,
                valid: !!value,
                error: '',
            };
        }
        this.setState(newState);
    };

    render() {
        const { password, email } = this.state;

        return (
            <div className="container-fluid login-wrapper">
                <Row className="align-items-center justify-content-center">
                    <Col xs="6" md="8" xl="4" sm="8" className="align-vertical-center">
                        <form onSubmit={this.onSubmit}>
                            <div className="logo">
                                <img src={require('assets/images/logo.png')} alt="Survefco" />
                            </div>
                            <Row>
                                <Col md="12">
                                    <InputField
                                        type="text"
                                        name="email"
                                        placeholder="Email"
                                        fieldClass="form-group col-12"
                                        inputFieldClass="form-control"
                                        onChange={this.handleOnChange}
                                        error={email.error}
                                        value={email.value}
                                    />
                                </Col>
                            </Row>
                            <Row>
                                <Col md="12">
                                    <InputField
                                        type="password"
                                        name="password"
                                        placeholder="Password"
                                        fieldClass="form-group col-12"
                                        inputFieldClass="form-control"
                                        onChange={this.handleOnChange}
                                        error={password.error}
                                        value={password.value}
                                    />
                                </Col>
                            </Row>
                            <Row>
                                <Col md="12">
                                    <button type="submit" className="btn btn-primary btn-lg btn-block rounded-2">
                                        Submit
                                    </button>
                                </Col>
                            </Row>
                        </form>
                    </Col>
                </Row>
                <Loader pastDelay={this.props.isFetching} />
            </div>
        );
    }
}

Login.propTypes = {
    history: PropTypes.object,
    loginUser: PropTypes.func,
    isFetching: PropTypes.bool,
    resetUserDetails: PropTypes.func,
};

const mapStateToProps = createSelector(
    [makeSelectUserDetails(), makeSelectUserErrorLogin(), makeSelectIsFetching()],
    (userDetails, errorUserLogin, isFetching) => ({
        userDetails,
        errorUserLogin,
        isFetching,
    })
);

const mapDispatchToProps = dispatch =>
    bindActionCreators(
        {
            ...loginActions,
        },
        dispatch
    );

export default connect(mapStateToProps, mapDispatchToProps)(Login);



