import React from 'react';
import classNames from 'classnames';
import PropTypes from 'prop-types';

import If from 'components/Shared/If';

const checkbox = 'checkbox';
const radio = 'radio';

const formatValue = value => value && value.replace(/^\s+/, '').replace(/\s+$/, ' ');

const InputField = ({
    name,
    label,
    type,
    error,
    inputFieldClass,
    fieldClass,
    placeholder = '',
    fieldId = '',
    onChange,
    onBlur,
    value,
    checked,
    disabled,
    tabIndex,
}) => {
    const fieldClassName = classNames({
        [`${fieldClass}`]: true,
        field: true,
        'form-check d-inline-flex': [radio, checkbox].includes(type),
    });
    const inputFieldClassName = classNames({
        [`${inputFieldClass}`]: true,
    });
    const checkMarkClassName = classNames({
        checkmark: type === checkbox,
        'checkmark-radio': type === radio,
    });

    const labelClassNames = classNames({
        'col-12 no-offset': ![radio, checkbox].includes(type),
        'form-control-label': type === radio || type === checkbox,
    });

    return (
        <div className={fieldClassName}>
            <label htmlFor={fieldId} className={labelClassNames} tabIndex={tabIndex || undefined}>
                <If condition={label}>
                    <span>{label}</span>
                </If>
                <input
                    name={name}
                    type={type}
                    id={fieldId}
                    className={inputFieldClassName}
                    placeholder={placeholder}
                    onChange={onChange}
                    onBlur={onBlur}
                    checked={type === checkbox || type === radio ? checked : undefined}
                    value={formatValue(value)}
                    disabled={disabled}
                    maxLength={type === 'text' || type === 'number' ? 255 : undefined}
                />
                <If condition={label && (type === checkbox || type === radio)}>
                    <span className={checkMarkClassName} />
                </If>
            </label>
            <If condition={error}>
                <span className="error">{error}</span>
            </If>
        </div>
    );
};

InputField.propTypes = {
    name: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    label: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    type: PropTypes.string.isRequired,
    error: PropTypes.string,
    inputFieldClass: PropTypes.string,
    fieldClass: PropTypes.string,
    placeholder: PropTypes.string,
    fieldId: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    onChange: PropTypes.func.isRequired,
    onBlur: PropTypes.func,
    value: PropTypes.string,
    checked: PropTypes.bool,
    disabled: PropTypes.bool,
    tabIndex: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
};

export default InputField;



