import { CALL_API, METHODS } from 'middleware';
import { LOGIN_ACTION_TYPES, RESET_USER_DETAILS } from './constants';

const loginUser = payload => ({
    [CALL_API]: {
        requestConfig: {
            url: 'signin',
            method: METHODS.POST,
            data: payload,
        },
        types: LOGIN_ACTION_TYPES,
    },
});

const resetUserDetails = () => ({
    type: RESET_USER_DETAILS,
});

export { loginUser, resetUserDetails };



