import get from 'lodash/get';
import { createSelector } from 'reselect';

export const selectIUser = state => get(state, 'user');

export const makeSelectUserDetails = () =>
    createSelector([selectIUser], userDetailState => get(userDetailState, 'userDetails', {}));

export const makeSelectUserErrorLogin = () =>
    createSelector([selectIUser], errorUserDetailState => get(errorUserDetailState, 'errorUserLogin', {}));

export const makeSelectIsFetching = () =>
    createSelector([selectIUser], isFetching => get(isFetching, 'isFetching', false));



